.NotFoundList {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px auto;
}

.NotFoundText {
    font-size: 30px;
    line-height: 1.7;
    color: #52555f;
    align-items: center;
}

.NotFoundButton {
    text-align: center;
    color: #52555f;
    background-color: #fff;
    border: 1px solid #e0e5eb;
    font-size: 15px;
    align-items: center;
    line-height: 1.16;
    display: inline-block;
    transition: all 0.3s;
    cursor: pointer;
    min-width: 44px;
    padding: 10px;
    border-radius: 10px;
    margin-top: 20px;
}

.NotFoundButton:hover,
.NotFoundButton:focus {
    cursor: pointer;
    transform: scale(1.1);
    background-color: #ff751d;
    color: #fff;
    transition: all 0.2s ease-out;
}

.NotFoundBackground {
    overflow: hidden;
    border-bottom-left-radius: 200px;
    background-color: #f2f5fc;
    position: absolute;
    top: 0;
    width: 100%;
    height: 582px;
    z-index: -1;
}

.NotFoundImg {
    width: 300px;
    margin-bottom: 10px;
}

/* Tablet */
@media screen and (min-width: 768px) {
    .NotFoundText {
        font-size: 20px;
        margin-top: 20px;
    }
}

/* Mobile */
@media screen and (max-width: 767px) {
    .NotFoundImg {
        width: 200px;
        margin-bottom: 0;
    }

    .NotFoundText {
        font-size: 15px;
        margin-top: 20px;
    }

    .NotFoundButton {
        font-size: 15px;
        min-width: 30px;
        padding: 8px;
        margin-top: 20px;
    }

    .background {
        height: 342px;
    }
}